.favourite-coins {
  // width: 80%;
  margin: 0 auto;
}

.fav-coin {
  transition: all 300ms;

  /* Rectangle 1 */
  display: flex;
  align-items: center;
  justify-content: center;
  // position: absolute;
  width: 76px;
  height: 37px;

  @media (min-width: 768px) {
    width: 152px;
    height: 67px;
  }
  // left: 189px;
  // top: 178px;

  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 13px;
  line-height: 127.34%;

  /* identical to box height, or 17px */
  text-align: center;
  text-transform: uppercase;

  color: #ffffff;

  /* white */
  border: 1px solid #ffffff;
  box-sizing: border-box;

  span {
    display: flex;
    align-items: center;
  }

  .fav-coin-image {
    margin-right: 0.5rem;
  }

  &:hover {
    background-color: #397bca;
    border-color: #397bca;
    cursor: pointer;

    span {
      display: none;
    }

    &:after {
      content: "REMOVE";
    }
  }
}

.fav-coin-padding {
  padding: 0.4375rem;
}

.fav-coin-list {
  position: absolute;
  width: 700px;
  display: flex;
  flex-wrap: wrap;
  overflow: none;
  height: 100px;
  overflow-y: scroll;

  .coin-full-name {
    display: none;
  }

  @media (min-width: 768px) {
    .coin-ticker {
      display: none;
    }

    .coin-full-name {
      display: block;
    }
  }

  @media (max-width: 768px) {
    width: 21.25rem;

    .fav-coin-padding {
      padding: 0.21875rem;
    }
  }
}

/* Favourite coins */
h1 {
  padding-bottom: 3.75rem;
  // position: absolute;
  width: 213px;
  height: 37px;
  left: 36px;
  top: 80px;

  font-family: Gibson;
  font-style: normal;
  font-weight: 600;
  font-size: 29px;
  line-height: 127.34%;

  /* identical to box height, or 37px */
  letter-spacing: 0.02em;
  text-transform: capitalize;

  /* white */
  color: #ffffff;

  @media (min-width: 768px) {
    padding-bottom: 5.375rem;
  }
}

h2 {
  /* No Favourite Coins Added */
  width: 158px;
  height: 17px;
  left: 36px;
  top: 140px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 132%;
  color: #ffffff;
}
